import { useHistory } from "react-router";
import {  Card } from "reactstrap";
import ExpenseModel from "../../../../../Models/ExpenseModel";
import "./ExpenseCard.css";

interface CardProps{
    expense: ExpenseModel;
};

function ExpenseCard(props:CardProps): JSX.Element {
  const id = props.expense.id;
  const history= useHistory();

return (
  <div className="ExpenseCard ">   
		    <Card  style={{ width: "17rem",height:"5rem", cursor:'pointer',borderRadius:"10px "}}  key={id} className="text-center">
            <button className="myButton" id="niceTitle" onClick={()=>history.push("/admin/expense/details/" + id)}>

             {props.expense.name}
            
            </button>

        </Card>  
  </div>
        
 );
};

export default ExpenseCard;
