import { useEffect } from "react";
import { useHistory } from "react-router";
import {  Card,CardTitle, UncontrolledTooltip } from "reactstrap";
import ClientType from "../../../../../Models/ClientTypeModel";
import store from "../../../../../Redux/Store";
import notify, { ErrMsg } from "../../../../../Services/Notification";
import ExpenseModel from "../../../../../Models/ExpenseModel";
import "./ExpenseCard.css";

interface CardProps{
    expense: ExpenseModel;
};

function ExpenseCard(props:CardProps): JSX.Element {

  const id = props.expense.id;
  const history= useHistory();
  const mydate = new Date(props.expense.purchaseDate);
  const day = mydate.getDay().toString();
  const month = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"][mydate.getMonth()];

  
  useEffect(() => {
    if (store.getState().authState.client?.clientType!==ClientType.user) {
        notify.error(ErrMsg.PLS_LOGIN);
        history.push("/login");
    };
  });

return (
  <div className="ExpenseCard customCard1">
		    <Card style={{ width: "20rem",height:"auto",margin:"0px",  backgroundColor: "lightblue"
 }}  key={id} className="text-center customCard1">
              <CardTitle tag="h4" id="customMargin" >
              <span id="niceTitle10"> {props.expense.name}</span> <br />
              <br />
              <span id="pd"> {props.expense.description} for {' '}</span> 
              <span id="price"> {props.expense.price}$</span> 

              <span id="customDate">
              <span >{month} </span> 
              <span>{''}</span>
              <span > {day}</span><br />
              <span id="fullYear">{mydate.getFullYear()}</span>
              </span>
            <UncontrolledTooltip target="#customDate">
             Purchase Date
            </UncontrolledTooltip>
              <br />
            </CardTitle>
            <button  
              className="pos"
              onClick={()=>history.push("/user/expense/edit/" + id)}>
              ✏️
            </button>
        </Card>  
  </div>      
 );
};

export default ExpenseCard;
